.Container
  width: 100%
  max-height: 100%
  overflow-y: hidden
  margin-left: 300px

.Forms
  width: 70%
  
.PageTitle
  padding: 40px 25px
  
.PageForm
  display: flex
  flex-direction: column
  width: 100%
  padding: 15px 50px
  margin: 0 auto

.LabelTitle
  width: 200px

.LabelColumns
  display: flex
  flex-direction: row
  justify-content: space-between

.LabelColumnsTitle
  margin-top: 30px

.Fields
  min-height: 300px

label
  padding: 20px 0
  display: flex
  justify-content: space-between

.Input
  width: 200px
  height: 25px
  padding-left: 5px

textarea
  width: 200px
  height: 100px
  resize: none
  padding-left: 5px
  padding-top: 5px

select
  width: 160px

.SpanError
  color: red

.CloseIcon
  cursor: pointer
  
  
